function getSteps() {
  return ['Book Information', 'Author Information', 'Payment Details','UploadPublication', 'UploadCover','Share Settings','Preview', 'Confirm and Finish'];
}

function getStepContent(stepIndex) {
  switch (stepIndex) {
    case 0:
      return getBookInformation();
    case 1:
      return getAuthorInformation();
    case 2:
      return getPricing();
    case 3:
      return getPublication();
      case 4:
        return getBookCover();
        case 5:
        return getSecuritySettings();
      case 6:
        return getPreview();
        case 7:
        
        return getConfirmation();

    default:
      return 'Uknown stepIndex';
  }
}

