// components/simple-dropzone.component.js
import React from "react";

import Dropzone from 'react-dropzone-uploader'
import 'react-dropzone-uploader/dist/styles.css'

const FileUpload = () => {

    // Payload data and url to upload files
    const getUploadParams = ({ meta }) => { return { url:'http://localhost:3000/api/upload' } }

    // Return the current status of files being uploaded
    const handleChangeStatus = ({ meta, file }, status) => { console.log(status, meta, file) }

    // Return array of uploaded files after submit button is clicked
    const handleSubmit = (files, allFiles) => {
        console.log(files.map(f => f.meta))
        allFiles.forEach(f => f.remove())
    }

    return (
      <Dropzone
      getUploadParams={getUploadParams}
      onChangeStatus={handleChangeStatus}
      onSubmit={handleSubmit}
      accept="image/*"
      maxFiles={1}
      inputContent="Upload Your Publication Here"
      // submitButtonDisabled={files => files.length < 2}
        />
    );


  //    // DropZone area for Pdf Uploads
  // return (
  //   <div className="dropzone-card">
  //     <div  className="dropzone">
  //       <div {...getRootProps({className: 'dropzone-file-btn'})}>
  //         <input {...getInputProps()} />
  //         <p>Drag 'n' drop some files here, or click to select files</p>
  //         <em>(Only *.jpg and png files will be accepted)</em>
  //       </div>
  //     </div>
  //   </div>

  
    
  // );

   
};

export default FileUpload;